.modal-header {
    border-bottom: none !important;
    padding: 24px 16px 0 15px;
    display: flex;
    justify-content: space-between;
    .modal-titlewf {
      color: #000;
      font-size: 18px;
      font-weight: 500;
      letter-spacing: 0.72px;
      line-height: 33px;
    }
  }
  button.close {
    border: none;
    background: transparent;
    outline: none;
    float: right;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1;
    color: #000;
    text-shadow: 0 1px 0 #fff;
  }
  .modal-body {
  
    button {
      font-size: 12px;
    }
  
    .dmog-tooltip {
      margin-left: 8px;
    }
    .heading {
      padding: 5px 8px;
      border-bottom: 1px solid black;
      font-size: 14px;
      color: black;
      font-weight: bold;
      letter-spacing: 0.7px;
  
      a {
          display: block;
          float: right;
          text-decoration: none;
          color: black;
  
          // .fa-angle-down {
          //     transform: rotate(0deg);
          //     -webkit-transform: rotate(0deg);
          //     transition: all 0.3s ease-out 0s;
          //     -webkit-transition: all 0.3s ease-out 0s;
          //     &::before{
          //         content: "\f107";
          //     }
          //}
          [class*="fa-"] {
              -webkit-text-stroke: 0.4px white;
              stroke: 0.4px white;
              display: inline-block;
              position: relative;
              right: -4px;
              top: 1px;
              transform: scale(2.5, 1.7);
              -webkit-transform: scale(2.5, 1.7);
              transition: all 0.3s ease-out 0s;
              -webkit-transition: all 0.3s ease-out 0s;
          }
      }
  }
  
  .heading.Hide {
      a {
          [class*="fa-"] {
              transform: rotate(-180deg) scale(2.5, 1.7);
              -webkit-transform: rotate(-180deg) scale(2.5, 1.7);
              ;
              transition: all 0.3s ease-out 0s;
              -webkit-transition: all 0.3s ease-out 0s;
          }
      }
  }
  }
  
