  .drag_grid.state-filter-wrap {
    .row {
        border-bottom: 0px !important;
    }
  }
  
  .drag_grid {
    border: 1px solid #C3BBB1;
  
    .row {
        display: table;
        width: 100%;
        margin-left: 0px;
        margin-right: 0px;
  
        &:first-child {
            border-bottom: 1px solid #C3BBB1;
        }
  
        .right_cell {
            border-left: 1px solid #dfdfdf;
        }
  
        .left_cell,
        .right_cell {
            display: table-cell;
            vertical-align: bottom;
            text-align: left;
            width: 50%;
            padding: 20px 15px;
  
            .wfMainGSearch {
                border: 1px solid #ccc;
                border-radius: 4px;
                overflow: hidden;
                max-width: 600px;
                margin: auto;
  
                .globalSearchWrap {
                    display: table;
                    width: 100%;
                }
            }
  
            .scroll_wrap {
                width: auto;
                height: 205px;
                overflow: auto;
  
                ul {
                    padding-right: 5px;
                    margin: 0px;
                    padding-left: 0px;
  
                    li {
                        display: table;
                        width: 100%;
                        border-bottom: 2px solid #dfdfdf;
                        cursor: default;
  
                        &:last-child {
                            border-bottom: 0px;
                        }
  
                        span {
                            display: table-cell;
                            vertical-align: middle;
                            text-align: left;
                            height: 26px;
                            font-size: 13px;
                            color: #4d4d4d;
  
                            &:last-child {
                                width: 50px;
                                text-align: right;
                            }
                        }
                    }
                }
            }
        }
    }
  }
  
  .drag_grid {
    .row {
        &:last-child {
            .left_cell {
                padding: 15px 15px !important;
            }
  
            .right_cell {
                padding: 15px 15px !important;
            }
        }
    }
  }
  
